import React from 'react';
import { useTranslation } from 'react-i18next';
import { Helmet } from "react-helmet";

import './index.scss';
import HeadingBlock from '../../components/HeadingBlock';
import {aboutUsListLeft, aboutUsListRight} from './list';
import { Link } from 'react-router-dom';

const AboutUs = React.memo(() => {

    const {t} = useTranslation();

    const onDownloadFile = (event) => {
        event.preventDefault();
        window.open('https://data-privacy-office.com/wp-content/uploads/2018/09/file.pdf');
    };

    const createJSONLD = () => {
        const data = {
            '@context': 'https://schema.org/',
            "@type": "Organization",
            "name": '24ROLLS',
            "description": aboutUsListLeft.concat(aboutUsListRight).map(el => t(`${el.text}`)).join(""),
            "founder": {
                "@type": 'Person',
                "name": t('aboutUs.contact.name'),
                "telephone": '+380507192424',
            },
            "location": {
                "@type": "Place",
                "address": {
                    "@type": 'PostalAddress',
                    "addressCountry": {
                        '@type': 'Country',
                        'name': 'Ukraine'
                    },
                    "addressRegion": 'Днепр, Запорожье'
                }
            },
            "url": 'https://www.youtube.com/embed/WlEp-P-5AVs'
        }

        return JSON.stringify(data);
    }

    return (
        <div className={'about-us'}>

            <Helmet>
                <meta name="keywords" content="Заказать суши, доставка, Днепр, Запорожье, Харьков, вкусно,  быстрая доставка" />
                <script type="application/ld+json">
                    {createJSONLD()}
                </script>
            </Helmet>

            <HeadingBlock title={t('aboutUs.title')} svg={true} />
            <div className={'mainText'}>
                <div className={'mainText_left'}>
                    {
                        aboutUsListLeft.map(el => <p key={el.id}>{t(`${el.text}`)}</p>)
                    }
                </div>
                <div className={'mainText_left'}>
                    {
                        aboutUsListRight.map(el => <p key={el.id}>{t(`${el.text}`)}</p>)
                    }
                </div>
            </div>
            <div className={'contact'}>
                <div className={'contact_header'}>
                    <h3>
                        {t('aboutUs.contactInformation')}
                    </h3>
                </div>
                <div className={'contact_center'}>
                    <span>
                        <pre>
                            <span>
                                {t('aboutUs.contact.name')}
                            </span>
                            <br />
                            <b>
                                {t('aboutUs.contact.phone.title')}
                            </b>
                            <br />
                            <span>
                                {t('aboutUs.contact.phone.1')}
                            </span>     
                            <span>
                                {' +380507192424'}
                            </span>
                            <br />
                            
                            <span>
                                {t('aboutUs.contact.phone.2')}
                            </span>              
                            <span>
                                {' +380507192424'}
                            </span>
                        </pre>
                         <br />
                        <b>{'email:'}</b> 
                        <span>
                            {' zp24rolls@gmail.com'}
                        </span>
                    </span>
                </div>
                <div className={'contact_header'}>
                    <h3>
                        {t('aboutUs.offer')}
                    </h3>
                </div>
                <div className={'contact_download'}>
                   <Link
                        download={'file.pdf'}
                        onClick={(event) => onDownloadFile(event)}
                    >
                        {t('aboutUs.offerSave.1')} <br />
                        {t('aboutUs.offerSave.2')}
                    </Link>
                </div>
            </div>
                <iframe
                    sandbox="allow-scripts allow-same-origin allow-popups-to-escape-sandbox"
                    title={'ROLLS24'}
                    src={'https://www.youtube.com/embed/WlEp-P-5AVs'}
                    frameBorder="0"
                    layout="responsive"
                />
        </div>
    )
});

export default AboutUs;